
import Header from './components/Header'
import Notes from './components/Notes'
import AddNote from './components/AddNote'

export default {
  name: 'App',
  components: {
    Header,
    Notes,
    AddNote
  },
  data() {
    return {
      notes: [],
      showAddNote: false
    }
  },
  methods: {
    toggleAddNote() {
      this.showAddNote = !this.showAddNote
    },
    addNote(note) {
      this.notes = [...this.notes, note]
    },
    deleteNote(id) {
      if(confirm('Do you sure?')) {
        this.notes = this.notes.filter((note) => note.id !== id)
      }
    },
    toggleReminder(id) {
      this.notes = this.notes.map((note) => note.id === id ? {...note, reminder: !note.reminder} : note)
    }
  },
  created() {
    this.notes = [
      {
        "id": "1",
        "text": "text-1",
        "day": "time-1",
        "reminder": true
      },
      {
        "id": "2",
        "text": "text-2",
        "day": "time-2",
        "reminder": true
      },
      {
        "id": "3",
        "text": "text-3",
        "day": "time-3",
        "reminder": false
      }
    ]
  }
}
